import React, { useState, useEffect } from 'react';
import Confetti from 'react-confetti';
import Navbar from '../Navbar/Navbar';
import im from '../../Assets/success.png';

const Confirm = () => {
  const [orderId, setOrderId] = useState('');

  useEffect(() => {
    // Generate a random order ID with 6 digits
    const randomId = Math.floor(100000 + Math.random() * 900000);
    setOrderId(randomId);
  }, []);

  return (
    <div>
      <Navbar />
      <br />
      <br />
      <br />
      <h1 style={{ textAlign: 'center' }}>Your Order has been placed successfully!</h1>
      <Confetti
        width={1800}
        height={1600}
        numberOfPieces={500}
        recycle={false}
      />
      <Confetti
        width={1800}
        height={1600}
        numberOfPieces={500}
        recycle={false}
      />
      <h1 style={{ textAlign: 'center' }}>Order ID: {orderId}</h1>
      <div style={{ display: 'flex', justifyContent: 'center' }}>
        <img style={{ transform: 'scale(0.5)' }} src={im} alt="" />
      </div>
    </div>
  );
};

export default Confirm;
